// 제품 상세페이지
// NHA-RETAIL-ORDER-002
.product_contents {
  width:100%; margin:0 auto;
  .swiper-button-next:after, .swiper-button-prev:after {display:none;}
}
.product_detail_type {
  // 메뉴 고정
  &.view_active {
    .fixed_menu {width:100%; position:fixed; top:80px; z-index:1;}
    .product_top_menu { display:block; }
    .substance_wrap {margin:80px 0 0;}
  }
  // 가격 푸터 고정
  &.price_active {
    #ProductPriceBar {position:absolute;bottom:-145px;}
  }
  // 옵션 고정
  &.option_active {
    #optionBar {position:fixed;top:280px;}
  }

  .view_detail {max-width:1200px;margin:0 auto;display:flex;padding:80px 0;}
  // slide img
  .detail_img {
    position:relative; width:100%; max-width:600px; height:600px; background-color:$gray1;
    @include borderRadius(30px);
    .slide {display:flex;align-items:center;justify-content:center;margin:0 auto;}
    .swiper-button-next, .swiper-button-prev {
      width:52px; height:52px;background:url(./../../images/common/slide_btn.png) 50% 50% no-repeat;
      background-size:cover; text-indent:-999999px;
    }
    .swiper-button-next {right:20px;}
    .swiper-button-prev {left:20px;@include transform(rotate(180deg));}
    .indicator {
      width:100%;bottom:-30px;text-align:center;position:absolute;z-index:1;
      .swiper-pagination-bullet {margin:0 4px;background-color:#e3e3e3;opacity:1;}
      .swiper-pagination-bullet-active {background-color:$gray5;}
    }
  }
  .info_form_wrap {
    width:100%; padding:10px 0 0 60px;
    .txt_elm {display:flex;flex-direction:column;}
    .variety {font-size:18px;font-weight:$medium;color:#3b56f2;}
    strong {padding:5px 0 0;font-size:50px;color:$gray7;font-weight:$regular;}
    .strong_p {padding:35px 0 0;font-size:22px;line-height:32px;color:$gray7;font-weight:$medium;}
    .desc {
      max-width:475px; padding:35px 0 0;font-size:16px;color:$gray5;
      line-height:28px;font-weight:$regular;
    }
    .img_box {
      padding:50px 0 0; margin:0 0 0 -15px;
      ul {display:flex;}
      li {
        width:100%; max-width:125px; min-width:110px; text-align:center;
        img {max-width:65px;}
        p {padding:5px 0 0;font-size:12px;color:$gray5;font-weight:$regular;line-height:14px;}
      }
    }
    .product_button_wrap {
      padding:55px 0 0; display:flex;
      a {
        width:100%;max-width:260px;padding:23px 0;text-align:center;font-size:16px;
        font-weight:$regular;color:$gray7;border:1px solid #cbcdcf;margin:0 0 0 2%;
        @include transition(border,.2s);
        &:hover, &:focus {border-color:$gray7;}
        @include borderRadius(8px);
        &:first-of-type { margin:0; }
        &:nth-of-type(1) {
          span {
            &:after {
              width:7px; height:10px; background:url(./../../images/common/view_deatails_icon.png) 50% 50% no-repeat;
              background-size:cover;
            }
          }
        }
        &:nth-of-type(2) {
          span {
            &:after {
              width:10px; height:10px; background:url(./../../images/common/download_brochure_icon.png) 50% 50% no-repeat;
              background-size:cover;
            }
          }
        }
        span {
          position:relative;display:inline-block;padding:0 20px 0 0;
          &:after {
            content:"";display:block;position:absolute; right:0;top:50%;
            @include transform(translate(0,-50%));

          }
        }
      }
    }
  }
  .product_top_menu {
    width:100%; top:80px; height:120px; background-color:$white; z-index:10;
    display:none;
    .menu_wrap {
      width:100%; max-width:1300px; margin:0 auto;display:flex; justify-content:space-between; padding:11px 50px;
      @include boxSizing;
    }
    .view_item {
      display:flex;
      align-items:center;
    }
    .img_item {
      width:95px;border:1px solid #e3e3e3;background-color:#f5f5f5;
      @include borderRadius(10px);
      img { width:100%; }
    }
    strong {font-size:28px;color:$gray7;font-weight:$medium; padding:0 0 3px 12px;}
    span {font-size:15px;color:$blue5;font-weight:$medium; display:block; padding:0 0 0 30px;}
    .product_button_wrap {
      align-items:center; display:flex;
      a {
        height:50px;padding:17px 20px; font-size:15px;font-weight:$regular;color:$gray7;
        display:block; margin:0 0 0 10px;
        &:hover, &:focus { span { color:$blue5; } }
        @include boxSizing;
        &:after {content:"";display:block;}
        &:nth-of-type(1) {
          span {
            &:after {
              width:7px; height:10px; background:url(./../../images/common/view_deatails_icon.png) 50% 50% no-repeat;
              background-size:cover;
            }
          }
        }
        &:nth-of-type(2) {
          span {
            &:after {
              width:10px; height:10px; background:url(./../../images/common/download_brochure_icon.png) 50% 50% no-repeat;
              background-size:cover;
            }
          }
        }
        span {
          position:relative;display:inline-block;padding:0 20px 0 0; color:$gray7;
          &:after {
            content:"";display:block;position:absolute; right:0;top:50%;
            @include transform(translate(0,-50%));

          }
        }

      }
    }
  }

  // 제품 구매 스텝 바
  .product_step_bar {
    width:100%; height:80px;background-color:black; z-index:2;
    ul {max-width:1300px;margin:0 auto;height:100%;display:flex; padding:0 50px; box-sizing:border-box;}
    li {
      height:100%;display:flex; padding:0 35px; justify-content:center;align-items:center;float:left;
      &:first-of-type { padding:0 35px 0 0; }
    }
    span {
      display:block;font-size:18px;font-weight:$regular;color:#787879; position:relative;
      padding:0 0 10px; margin:10px 0 0;
      em { padding:0 5px 0 0; }
      // 현재
      &.step_active {
        color:$white;font-weight:$bold;font-size:20px; margin:10px 0 3px;
        &:after {
          height:3px; width:105%; background-color:$blue5;content:"";display:block;
          position:absolute; bottom:0;left:50%;
          @include transform(translate(-50%,0));
        }
      }
      // 완료
      &.step_complete {
        padding:0 0 10px 31px; color:#e3e3e3;
        em {
          width:24px; height:24px; background:url(./../../images/common/step_complete_icon.png) 50% 50% no-repeat;
          background-size:cover;position:absolute; left:0; top:-2px;padding:0; text-indent:-99999px;
        }
      }
    }
  }

  // 내용
  .substance_wrap {
    position:relative; background-color:#f5f5f5;
    .substance_cont {padding:75px 0 80px;width:100%;max-width:1200px;margin:0 auto 145px;}
    .hardware_wrap {
      .list_item01_2 {max-width:385px;}
      .list_item01_3 {max-width:720px;}
    }
    .form_check_radio_type01 {
      &:first-of-type {
        .tit_wrap { &:first-of-type { padding:0; } }
      }
      ul {
        padding:15px 0 0;
        label {height:55px;border-color:#c5c5c6; font-size:14px; }
      }
    }
    .tit_wrap {padding:40px 0 0;}
  }

  // 가격 바
  #ProductPriceBar {
    position:fixed; bottom:0; width:100%; height:145px;border-top:2px solid $gray2; background-color:$white;z-index:2;
    .bar_wrap {display:flex; width:100%; max-width:1300px;margin:0 auto; justify-content:space-between; padding:41px 50px; box-sizing:border-box;}
    @include boxSizing;
    .calculate_bar {width:100%; max-width:750px; display:flex; justify-content:space-between;}
    .price_view{
      display:flex;
      li {
        position:relative;font-size:24px;color:$gray7;font-weight:$medium;padding:0 40px 0 60px;
        .option_txt {padding:13px 0 0;font-size:15px;color:$gray5;}
        &:first-of-type {
          padding:0 40px 0 0;
          &:before {display:none;}
        }
        &:before {
          width:31px; height:31px;content:"";display:block;
          background:url(./../../images/common/step_bar_plus_icon.png) 50% 50% no-repeat;
          background-size:cover;position:absolute;left:0;top:10px;
        }
      }
    }
    .total_price {
      display:flex;
      .price_p {color:$blue5;font-size:32px;}
      li {
        position:relative;font-size:24px;color:$gray7;font-weight:$medium;padding:0 40px 0 60px;
        .option_txt {padding:13px 0 0;font-size:15px;color:$gray5;}
        &:before {
          width:31px; height:31px;content:"";display:block;
          background:url(./../../images/common/step_bar_total_icon.png) 50% 50% no-repeat;
          background-size:cover;position:absolute;left:0;top:10px;
        }
      }
    }
    .info_btn {
      display:flex;
      li {
        margin:0 0 0 6px;
        &:first-of-type { margin:0; }
        &:hover, &:focus {
          button {border-color:$blue5;}
        }
        // 비활성화 상태
        &.btn_disabled {
          &:hover, &:focus {
            button {color:#b8b8b9;}
          }
          button {border:1px solid #b8b8b9;background-color:$white;cursor:default;color:#b8b8b9;}
        }
        &.download_btn.btn_disabled {
          &:before {
            background:url(./../../images/design_order/step_bar_download_icon_disabled.png) 50% 50% no-repeat;background-size:cover;
          }
          &:hover, &:focus {
            &:before {
              //background:url(./../../images/design_order/step_bar_download_icon_over.png) 50% 50% no-repeat;background-size:cover;
            }
          }
        }
        &.wish_btn.btn_disabled {
          &:before {
            background:url(./../../images/design_order/step_bar_heart_icon_disabled.png) 50% 50% no-repeat;background-size:cover;
          }
          &:hover, &:focus {
            &:before {
              //background:url(./../../images/design_order/step_bar_heart_icon_over.png) 50% 50% no-repeat;background-size:cover;
            }
          }
        }
        &.next_step_btn.btn_disabled {
          button {
            background-color:#b0cff9;cursor:inherit;color:$white;border-color:#b0cff9;
            &:hover, &:focus {background-color:#b0cff9;color:$white;}
          }
        }
      }
      // 활성화
      button {
        width:100%; height:100%; display:block; border:1px solid $gray7;
        @include boxSizing;
        @include borderRadius(30px);
        @include transition(all,.2s);
      }
      .download_btn, .wish_btn {
        width:62px; height:62px; position:relative;
        &:before {
          width:22px; height:19px; content:"";display:block;position:absolute; top:50%;left:50%;
          pointer-events:none;
          @include transform(translate(-50%, -50%));
          @include transition(all,.2s);
        }
      }
      .cart_btn,.next_step_btn {
        width:140px;
        button {font-size:16px; font-weight:$medium;}
      }
      // 개별
      .download_btn {
        &:before {
          background:url(./../../images/design_order/step_bar_download_icon.png) 50% 50% no-repeat;background-size:cover;
        }
        &:hover, &:focus {
          &:before {
            background:url(./../../images/design_order/step_bar_download_icon_over.png) 50% 50% no-repeat;background-size:cover;
          }
        }
      }
      .wish_btn {
        &:before {
          background:url(./../../images/design_order/step_bar_heart_icon.png) 50% 50% no-repeat;background-size:cover;
        }
        &:hover, &:focus {
          &:before {
            background:url(./../../images/design_order/step_bar_heart_icon_over.png) 50% 50% no-repeat;background-size:cover;
          }
        }
      }
      .cart_btn {
        &:hover, &:focus {
          button {color:$blue5;}
        }
      }
      .next_step_btn {
        &:hover, &:focus {
          button { background-color:#045fd5; }
        }
        button {color:$white; background-color:$blue5; border:none;box-shadow:5px 5px 22px #d7d7d7;}
      }
    }
  }

  // 옵션 바
  #optionBar {
    width:288px; height:713px; position:absolute; top:0; right:0; background-color:$white; z-index:1;
    @include boxSizing;
    @include boxShadow(0 0 24px rgba(15, 13, 13, .2));
    .option_title {
      border-bottom:1px solid $gray2; padding:0 37px;
      .default_box {
        padding:27px 0 26px; position:relative;
        h3 {font-size:20px; font-weight:$medium; color:$gray7;}
        .btn_clearFilters {
          position:relative;font-size:13px; line-height:24px;font-weight:$medium; color:$gray7;margin:15px 0 0;
          &:after {width:100%; height:1px; position:absolute; bottom:3px; left:0; background-color:$gray7; content:"";}
          &:hover, &:focus {
            color:$blue5;
            &:after {background-color:$blue5;}
          }
        }
      }
      .availability_box {
        border-top:1px solid $gray2; padding:28px 0;
        strong {font-size:20px; margin:0 0 20px; font-weight:$medium; color:$gray7; display:block;}
        p {
          display:flex; justify-content:space-between;
          button {
            width:49%; font-size:13px; line-height:36px; border-radius:30px; border:1px solid $gray3; color:$gray7; font-weight:$medium;
            @include transition();
            &:hover, &:focus, &.on {
              color:$white; background-color:$gray7; border-color:$gray7;
            }
          }
        }
      }
    }
    .option_wrap {
      width:100%; position:absolute; top:113px; left:0;padding:10px 10px;overflow-y:auto; background-color:$white;
      @include boxSizing;
    }
    .scroll_bar_custom {padding:0;}
    .option_cont {
      height:350px;
      @include boxSizing;
      .option_type {
        position:relative; padding:0 0 35px;
        @include boxSizing;
        &:last-of-type { &:after {display:none;} }
        &:after {
          width:212px; height:1px; background-color:#e3e3e3; content:"";display:block;
          position:absolute; bottom:0; left:50%;
          @include transform(translate(-50%,0));
        }
        strong {display:block; padding:20px 0 0 25px; font-size:16px;font-weight:$medium;color:$gray7;}
        ul {padding:20px 15px 0 25px;}
        li {
          font-size:14px;color:$gray5;font-weight:$regular; line-height:22px;position:relative; padding:0 0 0 10px;
          &:before {
            content:"·";display:inline-block;padding:0 5px 0 0;
            font-weight:$extra-bold;position:absolute; left:0;
          }
        }
        .code_row {
          margin:30px 25px 0 25px; font-size:14px;color:$gray7;font-weight:$medium;
          display:flex;justify-content:space-between;
          @include boxSizing;
        }
      }
    }
    .stock_message {
      width:100%; padding:60px 20px 0;
      @include boxSizing;
      span {
        display:block;width:100%; background-color:#f5f5f5;font-size:13px;font-weight:$medium;
        padding:10px 10px 10px 35px; line-height:16px;position:relative;
        &:before {
          width:17px; height:17px;content:"i";color:$white;background-color:#cecece;left:10px;
          display:block;text-align:center;font-weight:$regular;font-size:12px;position:absolute;
          @include borderRadius(20px);
        }
        @include boxSizing;
        @include borderRadius(10px);
        &.in_stock_txt {color:$blue5;}
        &.not_stock_txt {color:#ea4335;}
      }
    }
  }
}

// NHA-COMMON-PARTS_DETAIL(ORDER)-012
.product_detail_type {
  &.type01 {
    .info_form_wrap {
      max-width:450px;
      .variety {font-size:18px;font-weight:$medium;color:$blue7;}
      strong {padding:10px 0 0;font-size:40px;font-weight:$medium;}
    }
    .info_txt_wrap {
      padding:30px 0 0;
      & > li {
        display:flex;padding:20px 0;justify-content:space-between;border-top:1px solid #e3e3e3;
        font-size:16px;color:$gray5;line-height:25px;font-weight:$medium;
        span {
          &:last-of-type { padding:0 15px 0 0; }
        }
        .snb_s {
          width:100%;
          a {
            color:$gray5; display:block; width:100%; margin:0 0 20px; position:relative;
            &.on {
              &:after {@include transform(rotate(180deg));}
            }
            &:after {
              width:13px; height:8px;content:"";display:block;
              background:url(./../../images/common/select_arr_default.png) 50% 50% no-repeat;
              background-size:cover; position:absolute; right:11px; top:50%;
              @include transform(translate(0,-50%));
              @include transition(all, .2s);
            }
          }
        }
        .hidden_elm {
          width:100%;
          li {
            padding:15px 30px;
            background-color:#f5f5f5;
            font-size:14px;
            color:$gray7;
            font-weight:$regular;
            @include borderRadius(10px);
            @include boxSizing;
          }
        }
      }
    }
  }
}

